import CheckoutAddress from "@/Components/Checkout/CheckoutAddress";
import { GetToken } from "@/utilities/cookies";
import {
  CountryListApi,
  StateListApi,
} from "@/api/serversideaction/countryapi";
import { GetAddonsList } from "@/utilities/cookies";
import createMetadata from "@/Components/Seo/createMetadata";
export async function generateMetadata() {
  const title = "Choose Address";
  return createMetadata({
    title: title,
  });
}
export default async function page() {
  const [token, countryList, stateList, addonlist] = await Promise.all([
    GetToken(),
    CountryListApi(),
    StateListApi(),
    GetAddonsList(),
  ]);

  return (
    <CheckoutAddress
      token={token}
      countryList={countryList}
      stateList={stateList}
      addonlist={addonlist}
    />
  );
}
